.divfont-medium1,
.divfont-medium2,
.divhidden1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.divfont-medium2 {
  border-radius: var(--br-7xs);
  background: linear-gradient(
    60.21deg,
    #facc15,
    #db2777 33.33%,
    #7e22ce 66.67%,
    #5b21b6
  );
  padding: var(--padding-5xs) 1.05rem var(--padding-5xs) var(--padding-base);
  font-size: var(--font-size-sm);
}
.divhidden1 {
  position: absolute;
  top: calc(50% - 20px);
  right: 7rem;
  gap: var(--gap-base);
}
.divbackdrop-blur-md,
.nav {
  position: absolute;
  width: 100%;
  right: 0;
  left: 0;
}
.nav {
  top: 2.75rem;
  border-bottom: 1px solid var(--color-gray-100);
  box-sizing: border-box;
  height: 6.06rem;
  font-size: var(--font-size-mini);
  color: var(--color-aliceblue);
}
.nav:active {
  animation: 1s ease 0s infinite normal none shadow-pop-tr;
  opacity: 1;
}
.divbackdrop-blur-md {
  top: -2.12rem;
  height: 6.75rem;
  text-align: left;
  font-size: var(--font-size-2xs);
  color: var(--color-white);
}
.frame {
  position: relative;
  background-color: var(--color-gray-400);
  width: 100%;
  height: 371.11rem;
  align-items: center;
  text-align: center;
  font-size: var(--font-size-25xl);
  color: var(--color-aliceblue);
  font-family: var(--font-inter);
}
@media screen and (max-width: 420px) {
  .frame {
    opacity: 0;
  }
  .frame.animate {
    animation: 1s ease 0s 1 normal forwards fade-in;
  }
  @keyframes fade-in {
    0% {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
}
